            <el-col :span="24"> {{registerAllData.TotalAmount}} {{registerAllData.Total}}  {{registerAllData.OrderAmount}}</el-col>
          </el-row>
          <el-col :span="12">
            <el-row>
              <el-col :span="24">
                <span style="display: inline-block;width: 17%;vertical-align: top;"></span>
                <!--<span style="display: inline-block;width: 81%;vertical-align: bottom;min-height: 20px;">50</span>-->
                <span style="display: inline-block;width: 81%;vertical-align: bottom;min-height: 20px;">{{registerAllData.DrugRate}}</span>
              </el-col>
              <el-col :span="24">
                <span style="display: inline-block;width: 17%;vertical-align: top;"></span>
                <!--<span style="display: inline-block;width: 81%;vertical-align: bottom;min-height: 20px;">50</span>-->
                <span style="display: inline-block;width: 81%;vertical-align: bottom;min-height: 20px;">{{registerAllData.DoctorAdvice}}</span>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="10" :offset="2">
            <el-row>
              <el-col :span="24">{{registerAllData.UserName}} {{registerAllData.MobilePhone}}</el-col>
              <el-col :span="24">
                <span style="display: inline-block;width: 20%;vertical-align: top;"></span>
                <!--<span style="display: inline-block;width: 76%;vertical-align: bottom;min-height: 20px;">50</span>-->
                <span style="display: inline-block;width: 76%;vertical-align: bottom;min-height: 20px;">{{registerAllData.Address}}</span>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
        <el-row style="margin-top: 10px">
          <el-col :span="6">{{registerAllData.DoctorName}}</el-col>
          <el-col :span="6"></el-col>
          <el-col :span="6"></el-col>
          <el-col :span="6"></el-col>
        </el-row>

        <!---->
        <div id="chenxiPrint" style="display: none">
          <!--<div id="chenxiPrint">-->
          <table width="100%" style="font-size: 12px">

            <!---->
            <tbody>
            <tr>
              <td><span v-text="registerAllData.Code"></span></td>
              <td colspan="2" align="right"><span v-text="registerAllData.CreatedOnTime"></span></td>
            </tr>
            <tr>
              <td height="50" style="font-size: 20px;margin: 20px 0"><b>{{registerAllData.StoreName}}</b></td>
            </tr>
            <tr height="26">
              <td>{{registerAllData.UserName}} {{registerAllData.SexName ? registerAllData.SexName : '__'}} {{registerAllData.BirthDate}}</td>
              <td width="50%">
                <!--<p style="padding-left: 70px"><span>{{registerAllData.DepartmentTypeName}}</span></p>-->
              </td>
            </tr>
            <tr>
              <td colspan="2">
                <div style="position: relative;margin-left: 60px;border-bottom: 1px solid #333">
                  <span style="position: absolute;bottom: 0;left: -60px; width: 60px"></span>
                  <p style="margin-bottom: 3px;min-height: 20px">{{registerAllData.DiseaseInfo}}</p>
                </div>
              </td>
            </tr>
            <tr>
              <td v-if="registerAllData.SaleOrderItems">RP{{registerAllData.SaleOrderItems[0].CategoryName.substring(4)}}</td>
              <td align="right" height="26">{{registerAllData.SaleOrderItems ? registerAllData.SaleOrderItems.length : ''}} </td>
            </tr>
            </tbody>

            <!--loop-->
            <tbody>
            <tr>
              <td colspan="2">
                <ul style="list-style-type: none;padding: 0;margin: 0;min-height: 260px;border-bottom: 1px solid #333;">
                  <li v-for="item in registerAllData.SaleOrderItems" :key="item.ProductId"
                      style="float: left;width: 33%;text-align: center;height: 24px;line-height: 24px">
                    <span style="display: inline-block;width: 70px;text-align: right;margin-right: 5px">{{item.ProductName}}</span>
                    <span style="display: inline-block;">{{item.RefundableQty}}</span> {{item.Unit}}
                  </li>
                </ul>
              </td>
            </tr>
            </tbody>

            <!---->
            <tbody>
            <tr>
              <td colspan="2" height="26">
                <div style="position: relative;margin-left: 36px">
                  <span style="position: absolute;bottom: 0;left: -36px; width: 36px"></span>
                  <p> {{registerAllData.TotalAmount}} {{registerAllData.Total}}  {{registerAllData.OrderAmount}}</p>
                </div>
              </td>
            </tr>
            <tr valign="top">
              <td width="48%">
                <div style="position: relative;margin-left: 36px">
                  <span style="position: absolute;top: 0;left: -36px; width: 36px"></span>
                  <p style="min-height: 24px">{{registerAllData.DrugRate}}</p>
                </div>
              </td>
              <td width="48%" style="margin-left: 4%">
                <div style="position: relative;margin-left: 98px">
                  <span style="position: absolute;top: 0;left: -48px; width: 48px"></span>
                  <p style="min-height: 24px">{{registerAllData.UserName}} {{registerAllData.MobilePhone}}</p>
                </div>
              </td>
            </tr>
            <tr valign="top">
              <td width="48%" height="24">
                <div style="position: relative;margin-left: 36px">
                  <span style="position: absolute;top: 0;left: -36px; width: 36px"></span>
                  <p style="min-height: 24px">{{registerAllData.DoctorAdvice}}</p>
                </div>
              </td>
              <td width="48%" style="margin-left: 4%">
                <div style="position: relative;margin-left: 96px">
                  <span style="position: absolute;top: 0;left: -36px; width: 36px"></span>
                  <p style="min-height: 24px">{{registerAllData.Address}}</p>
                </div>
              </td>
            </tr>
            <tr>

              <!---->
            <tr>
              <td height="26"><span v-text="registerAllData.Code">{{registerAllData.DoctorName}}</span></td>
              <td colspan="2"><p style="padding-left: 50px"><span></span></p></td>
            </tr>
            <tr>
              <td height="26"><span></span></td>
              <td colspan="2"><p style="padding-left: 50px"><span></span></p></td>
            </tr>
            </tbody>
          </table>
        </div>

      </el-aside>

      <el-main>
        <div class="ownScrollbar" style="min-height: 390px;overflow-y: scroll;"
             v-loading="dataListLoading">
          <div style="">
            <el-row>
              <el-col :span="24">{{registerAllData.UserCode}}</el-col>
            </el-row>
          </div>

          <el-row>
            <el-col><div style="padding-top: 5px;font-size: 16px;font-weight: 900;color: #1CA579"></div></el-col>
          </el-row>
          <div style="padding-left: 32px">
            <!---->
            <el-row style="text-align: center; font-weight: 700; border: 1px solid #ccc; border-left: none; border-right: none;height: 35px;line-height: 35px">
              <el-col :span="6"></el-col>
              <el-col :span="6"></el-col>
              <el-col :span="6"></el-col>
              <el-col :span="6"></el-col>
            </el-row>
            <!---->
            <el-row style="text-align: center;border-bottom: 1px solid #ccc; height: 30px;line-height: 30px;clear: both">
              <el-col :span="6"></el-col>
              <el-col :span="6">{{registerAllData.RegisterAmount}}</el-col>
              <el-col :span="6">{{registerAllData.RegisterStatusName ? registerAllData.RegisterStatusName : ''}}</el-col>
              <el-col :span="6">- -</el-col>
            </el-row>
            <el-row style="text-align: center;border-bottom: 1px solid #ccc; height: 30px;line-height: 30px;clear: both">
              <el-col :span="6"></el-col>
              <el-col :span="6">{{registerAllData.ConsultationAmount}}</el-col>
              <el-col :span="6">{{registerAllData.RegisterStatusName ? registerAllData.RegisterStatusName : ''}}</el-col>
              <el-col :span="6">- -</el-col>
            </el-row>
            <el-row style="text-align: center;border-bottom: 1px solid #ccc; height: 30px;line-height: 30px;clear: both">
              <el-col :span="6"></el-col>
              <el-col :span="6">{{registerAllData.TotalAmount * registerAllData.Total}}</el-col>
              <el-col :span="6">{{registerAllData.StatusName ? registerAllData.StatusName : ''}}</el-col>
              <el-col :span="6">
                <!--<span style="color: #409EFF;cursor: pointer;text-align: center">- -</span>-->
                <span @click="seeRecipelInfo" style="color: #409EFF;cursor: pointer;text-align: center">{{isAddActive === false ? '' : ''}}</span>
              </el-col>
            </el-row>
          </div>

          <el-form ref="dataForm" :rules="dataRule" :model="dataForm" label-width="70px" size="mini" :inline="true">
            <el-row style="margin-top: 30px;text-align: left;font-weight: 500; font-size: 16px">
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="residualPrice" placeholder="" style="width: 100px" disabled size="small"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="">
                  <el-select v-model="dazhe" style="width: 100px">
                    <el-option v-for="item in dazheArr" :key="item.value"
                               :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="daijian" style="width: 100px" size="small"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="zhiwan" style="width: 100px" size="small"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="kuaidi" style="width: 100px" size="small"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="qita" style="width: 100px" size="small"></el-input>
                  <!--<el-input-number v-model="dataForm.reality" :max="10" label=""></el-input-number>-->
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="8" style="margin-bottom: 10px">
                <el-form-item label=""><!---->
                  <el-select v-model="dataForm.PaymentWay" style="width: 100px" placeholder="">
                    <el-option v-for="item in optionsPaymentType" :key="item.value"
                               :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="" prop="reality">
                  <el-input @blur="realityBlur" v-model="dataForm.reality" style="width: 100px" size="small"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="" prop="give">
                  <el-input v-model="dataForm.give" style="width: 100px" clearable size="small" disabled></el-input>
                </el-form-item>
              </el-col>
            </el-row>

          </el-form>
        </div>
      </el-main>

    </el-container>

    <div style="text-align: right">
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dataFormSubmitA()"></el-button>
        <el-button @click="visible = false"></el-button>
      </span>
    </div>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import '../common/icon/iconfont.css'
import {calcAge, Currency} from '@/utils/validate'

export default {
  data () {
    return {
      visible: false,
      dataListLoading: false, // 
      addOrUpdateVisible: false, // 
      isAddActive: true,

      registerAllData: '', // 
      regMoney: /^\d+\.?\d{0,2}$/,
      dataForm: {
        reality: '', // 
        give: '', // 
        PaymentWay: 1 // 
      },
      dataRule: {
        reality: Currency('')
      },
      optionsPaymentType: [ // 
        {
          value: 1,
          label: ''
        }, {
          value: 2,
          label: ''
        }, {
          value: 3,
          label: ''
        }, {
          value: 4,
          label: ''
        }
      ],
      dazhe: 1.00,
      dazheArr: [ // options
        {
          value: 1.00,
          label: ''
        }, {
          value: 0.95,
          label: '95'
        }, {
          value: 0.90,
          label: '9'
        }, {
          value: 0.80,
          label: '8'
        }, {
          value: 0.70,
          label: '7'
        }
      ],
      daijian: 0,
      zhiwan: 0,
      kuaidi: 0,
      qita: 0,
      //   OerderAmount(21)
      residualPrice: 0 // 2019.06.01 
    }
  },
  watch: {
    'dataForm.reality': function (newval, oldval) {
      console.log(this.residualPrice)
      if (Number(newval) === 0) { return false }
      if (!this.regMoney.test(newval)) {
        this.$alert('! ', '', {
          confirmButtonText: '',
          callback: () => {
            this.dataForm.reality = ''
            this.dataForm.give = ''
            return false
          }
        })
      }
      if (Number(newval) < this.residualPrice) { // realityBlur
        return false
      } else {
        this.dataForm.give = Math.round((newval - this.residualPrice) * 100) / 100 // 
      }
    }
  },
  methods: {
    realityBlur () {
      if (Number(this.dataForm.reality) < this.residualPrice) {
        this.dataForm.reality = ''
        this.dataForm.give = ''
        this.$message({
          type: 'warning',
          duration: '5000',
          message: `:   !`
        })
      }
    },
    // Id
    init (formId) {
      if (formId) {
        this.visible = true
        this.dataListLoading = true
        API.register.getRegisterInfo({id: formId}).then(result => {
          if (result.code === '0000') {
            result.data.BirthDate = calcAge(result.data.BirthDate)
            this.registerAllData = result.data
            this.dataListLoading = false
            console.log('', result.data)

            // 2019.06.01   OerderAmount(21)
            if (this.registerAllData.RegisterStatus === 2) {
              this.residualPrice = Number((this.registerAllData.OrderAmount - this.registerAllData.RegisterAmount - this.registerAllData.ConsultationAmount).toFixed(2))
            } else if (this.registerAllData.RegisterStatus === 1) {
              this.residualPrice = this.registerAllData.OrderAmount
            }
            console.log(this.residualPrice)
          }
        })
      }
    },
    seeRecipelInfo () {
      this.isAddActive = !this.isAddActive // '[]'
    },
    handleClose () {
      this.isAddActive = true
      this.dataForm = {
        reality: '', // 
        give: '', // 
        PaymentWay: 1 // 
      }
    },
    // 
    dataFormSubmitA () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          if (this.dataForm.reality === 0 && this.residualPrice > 0) {
            this.$alert('! ', '', {
              confirmButtonText: ''
            })
            return false
          }
          var params = {
            id: this.registerAllData.Id,
            PaymentWay: this.dataForm.PaymentWay, // 
            ActualAmount: this.residualPrice //  this.dataForm.reality 
          }
          console.log(params)
          API.register.cashierSubmit(params).then(result => {
            if (result.code === '0000') {
              this.$message({
                message: `${''}`,
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.visible = false
                  this.$emit('refreshDataList')
                }
              })
            } else {
              this.$message.error(result.message)
            }
          })
          this.chenxiPrint() // 
        } else {
          this.$alert('! ', '', {
            confirmButtonText: ''
          })
        }
      })
    },
    // 
    chenxiPrint () {
      var printHTML = document.getElementById('chenxiPrint').innerHTML // 
      var page = window.open('', '_blank') // 
      page.document.write(printHTML) // 
      page.print() // 
      var userAgent = navigator.userAgent
      if ((userAgent.indexOf('compatible') > -1 && userAgent.indexOf('MSIE') > -1) || (userAgent.indexOf('Edge') > -1) || (userAgent.indexOf('Trident') > -1 && userAgent.indexOf('rv:11.0') > -1)) {
        // IE
        page.document.execCommand('print')
      } else {
        console.log('not IE')
      }
      page.close() // 
      // var myPrint = document.getElementById('chenxiPrint').innerHTML
      // // console.log(myPrint)
      // var bodyHTML = document.body.innerHTML
      // window.document.body.innerHTML = myPrint
      // window.print()
      // window.document.body.innerHTML = bodyHTML
    }
    // 

  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.charge-info /deep/ .el-form-item {
  margin-bottom: 0;
}
.ownScrollbar /deep/ {
  span {
    display: inline-block;
    width: 70px;
    text-align: right;
    height: 30px;
    line-height: 30px;
  }
}

/* */
.charge-info /deep/ {
  .el-dialog__body {

  }

  .el-radio-button--mini .el-radio-button__inner {
    padding: 7px 9px;
  }
  /**/
  .v-enter {opacity: 0}
  .v-enter-active {transition: all .4s ease}
  .v-leave-active {position:absolute}
}
.ownScrollbar::-webkit-scrollbar,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar {
  width: 7px;
}
.ownScrollbar::-webkit-scrollbar-thumb,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-thumb {
  border-radius: 3px;
  box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
  background-color: #DDDEE0;
}
.ownScrollbar::-webkit-scrollbar-track,
.charge-info /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-track {
  border-radius: 0;
  box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: rgba(0,0,0,0);
}
</style>



